body {
  background-color: #7D7768;
  font-family: 'Asap', Sans-serif;
  font-size: 0.95em; }

.content {
  max-width: 900px;
  padding: 0; }

h1,
h2,
h3,
h4,
h5 {
  text-align: center; }

.column {
  width: 50%;
  min-width: 275px; }

.page-header {
  background: url(/moogwein/static/media/hang.aeec0dde.jpg) top left no-repeat;
  height: 600px;
  padding: 0px 20px; }

.logo {
  padding: 10px 0px 0px 10px; }
  .logo img {
    width: 94px; }

.float-left {
  float: left; }

.menu {
  z-index: 9000;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: none;
  max-width: initial;
  background-color: rgba(45, 97, 85, 0.85); }
  .menu h2,
  .menu .nav__item > a {
    color: white; }
  .menu .menu-container {
    padding: 0 10px;
    justify-content: space-between;
    display: flex;
    max-width: 900px;
    width: 100%; }

.toolbar {
  width: 100%;
  background-color: rgba(45, 97, 85, 0.8); }
  .toolbar h2,
  .toolbar .nav__item > a {
    color: white; }

.content {
  background-color: white; }

.footer {
  background-color: rgba(45, 97, 85, 0.8);
  width: 100%;
  padding: 10px;
  text-align: center;
  color: white; }

